import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import * as dat from "dat.gui";

const noeudScene = new THREE.Scene();

const noeudCamera = new THREE.PerspectiveCamera(
  35,
  window.innerWidth / window.innerHeight,
  0.1,
  3000
);
noeudCamera.position.z = 100;
noeudScene.add(noeudCamera);

let noeudRender = new THREE.WebGLRenderer({
  canvas: document.getElementById("noeud-object"),
  alpha: true,
  antialias: true,
});
noeudRender.setSize(window.innerWidth / 3, window.innerHeight / 3);
noeudRender.setClearColor(0xff7f50, 0);

const noeud = new THREE.Group();

const noeudGeometry = new THREE.TorusKnotGeometry(15, 5, 50, 20);

const noeudMaterial = new THREE.MeshNormalMaterial({
  transparent: true,
  opacity: 1,
  wireframe: true,
  wireframeLinewidth: 5,
});

noeud.add(new THREE.Mesh(noeudGeometry, noeudMaterial));

noeudScene.add(noeud);

const sizes = {
  width: window.innerWidth / 3,
  height: window.innerHeight / 4,
};

window.addEventListener("resize", () => {
  // Update sizes
  sizes.width = window.innerWidth / 3;
  sizes.height = window.innerHeight / 4;

  // Update camera
  noeudCamera.aspect = sizes.width / sizes.height;
  noeudCamera.updateProjectionMatrix();

  // Update renderer
  noeudRender.setSize(sizes.width, sizes.height);
  noeudRender.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/* ********** Animate ********** */
const controls = new OrbitControls(
  noeudCamera,
  document.getElementById("noeud-object")
);
controls.update();

animate();

function animate() {
  requestAnimationFrame(animate);

  noeud.rotation.x += 0.005;
  controls.update();

  noeudRender.render(noeudScene, noeudCamera);
}
